.section {
  padding: 24px 16px;
  @media screen and (min-width: 744px) {
    padding: 40px;
  }
  @media screen and (min-width: 1200px) {
    padding: 80px 0 80px 80px;
  }
  &__content {
    margin-top: 24px;
    @media screen and (min-width: 744px) {
      margin-top: 80px;
    }
  }
}
